import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';

import { connect, Provider } from 'react-redux';
import store from './app/store';
import { ThemeProvider } from '@material-ui/core/styles';
import { getTheme } from './app/selectors/ui';
import themeGenerator from './app/theme';
import { CssBaseline } from '@material-ui/core';
import DateFnsUtils from '@date-io/date-fns';
import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import * as serviceWorker from './app/serviceWorker';
//import { Router } from "react-router-dom";

if (process.env.NODE_ENV === 'development') {
  const { worker } = require('./app/mocks/browser');
  worker.start();
}

const mapStateToProps = state => ({ theme: getTheme(state) });

const ThemedApp = connect(mapStateToProps)(({ theme }) => {
  const generatedTheme = themeGenerator(theme);
  return (
    <ThemeProvider theme={generatedTheme}>
      <MuiPickersUtilsProvider utils={DateFnsUtils}>
        <CssBaseline />
        {/* <Router> */}
          <App />
        {/* </Router> */}
      </MuiPickersUtilsProvider>
    </ThemeProvider>
  );
});

ReactDOM.render(
  <Provider store={store}>
    <ThemedApp />
  </Provider>,
  document.getElementById('root')
);

serviceWorker.unregister();