import { FETCH_CITIES } from "../constants/actions";

const initialState = {
    byId: [],
    isFetching: false
};

const cities = (state = initialState, action) => {

    switch (action.type) {

        case FETCH_CITIES.PENDING:
            return { ...state, isFetching: true };

        case FETCH_CITIES.SUCCESS:
            const cities = action.payload;
            return { ...state, byId: cities, isFetching: false, lastFetch: action.meta.time };

        case FETCH_CITIES.ERROR:
            return { ...state, isFetching: false };

        default: { return state; }
    }
};

export default cities;