import React, { useEffect } from 'react';
import { connect } from "react-redux";
import Container from '@material-ui/core/Container';
import Card from '@material-ui/core/Card';
import Checkbox from '@material-ui/core/Checkbox';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import { makeStyles } from '@material-ui/core/styles';
import { Button, Divider, Grid, Typography } from '@material-ui/core';
import Chip from '@material-ui/core/Chip';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { KeyboardDatePicker } from '@material-ui/pickers';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

import Autocomplete from '@material-ui/lab/Autocomplete';

import { monent } from 'moment';

//actions
import {
    addMeetup,
    clearMeetup,
    deleteMeetup,
    mountedMeetupDashboardComponent as mounted,
    getEstimatedCount,
    subscribeMeetup,
    iWasHereMeetup
} from '../actions/meetup';

import { getWeatheCity } from '../actions/weather';

//selectors
import { getMeetup } from '../selectors/meetups';
import { getCurrentLocation } from '../selectors/location';
import { getUsuariosSlice } from '../selectors/usuarios';
import { getCities } from '../selectors/cities';
import { getWeather } from '../selectors/weather';


const MeetupDashboard = ({
    meetups,
    usuarios,
    cities,
    weather,
    location,
    addMeetup,
    deleteMeetup,
    mounted,
    getEstimatedCount,
    subscribeMeetup,
    iWasHereMeetup,
    getWeatheCity
}) => {

    const useStyles = makeStyles((theme) => ({
        formControl: {
            margin: theme.spacing(2),
            minWidth: 200,
        },
        card: {
            maxWidth: 600
        },
        selectEmpty: {
            marginTop: theme.spacing(2),
        },
        select: {
            width: 200
        },
        grid: {
        },
        button: {
            margin: theme.spacing(1),
        }
    }));

    const classes = useStyles();

    useEffect(() => {
        mounted();
        clearMeetup();
    }, [meetups]);

    const moment = require('moment');

    const dias = [
        { id: 1, name: moment().add(1, 'day').format("DD/MM/YYYY") },
        { id: 2, name: moment().add(2, 'day').format("DD/MM/YYYY") },
        { id: 3, name: moment().add(3, 'day').format("DD/MM/YYYY") },
        { id: 4, name: moment().add(4, 'day').format("DD/MM/YYYY") },
        { id: 5, name: moment().add(5, 'day').format("DD/MM/YYYY") }
    ];

    const [selectedUsers, setSelectedUsers] = React.useState([]);
    const [selectedDay, setSelectedDay] = React.useState(-1);
    const [selectedCity, setSelectedCity] = React.useState(-1);

    const [meetup, setMeetup] = React.useState({
        name: "",
        fecha: "",
        countPerson: "",
        countSixPacks: "",
        users: [],
        usersAttended: [],
        temp: "",
        city: ""
    });

    const [errors, setErrors] = React.useState({
        name: "",
        fecha: "",
        countPerson: "",
        countSixPacks: "",
        users: [],
        temp: "",
        city: ""
    });

    const [open, setOpen] = React.useState(false);

    const handleAddMeetup = () => {

        let validation = validateMeetup();

        console.log("validation", validation)
        if (Object.keys(validation).length === 0) {
            const m = {
                ...meetup,
                users: selectedUsers,
                usersAttended: [],
                countSixPacks: meetups.estimatedCount,
                temp: (weather.daily === undefined) ? "" : Math.trunc(weather.daily[selectedDay].temp.day)
            }
            console.log("add meetup", m)
            addMeetup(m);
            setOpen(false);
        }

        setErrors(validation);

    };

    const handleCalculateEstimatedBeer = () => {

        let validation = validateMeetup();

        if (Object.keys(validation).length === 0) {
            const m = {
                countPerson: meetup.countPerson,
                temp: weather.daily[selectedDay].temp.day
            };
            getEstimatedCount(m);
        }
        setErrors(validation);
    };

    const handleClickOpen = () => {
        clearMeetup();
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const clearMeetup = () => {
        setSelectedUsers([]);
        setSelectedDay(0);
        setMeetup({
            name: "",
            fecha: "",
            countPerson: "",
            countSixPacks: "",
            users: [],
            usersAttended: [],
            temp: ""
        });
        setErrors({
            name: "",
            fecha: "",
            countPerson: "",
            countSixPacks: "",
            users: [],
            usersAttended: [],
            temp: ""
        });
    };

    const handleOnUserSelectChange = (value) => {
        setSelectedUsers(value === undefined ? [] : value);
    };

    const handleSubscribeMeetup = (value) => {

        const meetupName = value.name;
        const myUserName = usuarios.username;

        subscribeMeetup(meetupName, myUserName);
    };

    const handleIwasHereMeetup = (value) => {

        console.log('handleIwasHereMeetup', value)
        const meetupName = value.name;
        const myUserName = usuarios.username;

        iWasHereMeetup(meetupName, myUserName);
    };

    const handleDeleteMeetup = (meetup) => {
        deleteMeetup(meetup);
    };

    const handleonChangeSelectDay = (value) => {
        setSelectedDay(value);
        setMeetup({ ...meetup, fecha: moment().add(value, 'day').format("DD/MM/YYYY") });
    };

    const handleonChangeSelectCity = (value) => {
        const city = cities.find(city => city.id == value);
        console.log("city", city)
        setMeetup({ ...meetup, city: city.name });
        getWeatheCity({ lat: city.lat, lon: city.lon, id: city, name: city.name });
    };

    const validateMeetup = () => {

        const validation = {};
        if (meetup.name === undefined || meetup.name.length < 1) {
            validation.name = "Requerido";
        }

        console.log('validation meetup', meetup)
        if (meetup.fecha === undefined || meetup.fecha.length < 1) {
            validation.fecha = "Requerido";
        }

        if (meetup.city === undefined || meetup.city.length < 1) {
            validation.city = "Requerido";
        }

        if (meetup.countPerson === undefined || meetup.countPerson.length < 1) {
            validation.countPerson = "Requerido";
        }

        return validation
    }

    console.log("cities", cities)

    return <Container>
        {(usuarios.rol === "ADMIN") ?
            <Button variant="outlined" color="primary" onClick={handleClickOpen} className={classes.button}>
                Crear meetup
        </Button>
            : ""
        }
        <Dialog open={open} onClose={handleClose} maxWidth="sm">
            <DialogTitle>Crear meetup</DialogTitle>
            <DialogContent>
                <form onSubmit={e => e.preventDefault()}>
                    <Grid container spacing={2}>
                        <Grid item sm={6}>
                            <FormControl
                                style={{ marginTop: 0 }}
                                error={Boolean(errors.fecha) ? true : false}
                                fullWidth>
                                <TextField
                                    label={"Nombre"}
                                    fullWidth
                                    name='name'
                                    value={meetup.name}
                                    onChange={event => setMeetup({ ...meetup, "name": event.target.value })}
                                    error={Boolean(errors.name)}
                                    helperText={errors.name}
                                />
                            </FormControl>
                        </Grid>
                        <Grid item sm={6}>
                            <Autocomplete
                                multiple
                                id="users-selector"
                                value={selectedUsers}
                                options={usuarios.byId}
                                disableCloseOnSelect
                                getOptionLabel={(option) => option.username}
                                renderOption={(option, { selected }) => (
                                    <React.Fragment>
                                        <Checkbox
                                            style={{ marginRight: 8 }}
                                            checked={selected}
                                        />
                                        {option.username}
                                    </React.Fragment>
                                )}
                                onChange={(_, newValue) => handleOnUserSelectChange(newValue)}
                                renderInput={(params) => (
                                    <TextField {...params}
                                        variant="outlined" label="Invitados" placeholder="" />
                                )} />
                        </Grid>
                        <Grid item sm={12}>
                            <TextField
                                label={"Cantidad de personas"}
                                fullWidth
                                name='countPerson'
                                type="number"
                                value={meetup.countPerson}
                                onChange={event => setMeetup({ ...meetup, "countPerson": event.target.value })}
                                error={Boolean(errors.countPerson)}
                                helperText={errors.countPerson}
                            />
                        </Grid>

                        <Grid item sm={12}>
                            <FormControl
                                style={{ marginTop: 0 }}
                                error={Boolean(errors.fecha) ? true : false}
                                fullWidth>
                                <InputLabel>Fecha</InputLabel>
                                <Select
                                    label={"Fecha"}
                                    fullWidth
                                    name='fecha'

                                    onChange={event => handleonChangeSelectDay(event.target.value)}
                                    error={Boolean(errors.fecha)}
                                    helperText={errors.fecha}>
                                    {
                                        dias.map(d =>
                                            <MenuItem value={d.id} key={d.id}>{d.name}</MenuItem>)
                                    }
                                </Select>
                                <FormHelperText>{errors.fecha || " "}</FormHelperText>
                            </FormControl>

                        </Grid>
                        <Grid item sm={12}>
                            <FormControl
                                style={{ marginTop: 0 }}
                                error={Boolean(errors.city) ? true : false}
                                fullWidth>
                                <InputLabel>Ciudad</InputLabel>
                                <Select
                                    label={"Ciudad"}
                                    fullWidth
                                    name='city'
                                    value={meetup.city}
                                    options={cities}
                                    onChange={event => handleonChangeSelectCity(event.target.value)}
                                    error={Boolean(errors.city)}
                                    helperText={errors.city}>
                                    {
                                        cities.map(city =>
                                            <MenuItem value={city.id} key={city.id}>{city.name}</MenuItem>)
                                    }
                                </Select>
                                <FormHelperText>{errors.city || " "}</FormHelperText>
                            </FormControl>
                        </Grid>
                        <Grid item sm={6}>
                            <FormControl
                                fullWidth>
                                <InputLabel>Temperatura</InputLabel>
                                <TextField
                                    label={"Temperatura"}
                                    disabled
                                    name='temp'
                                    value={meetup.temp}
                                />
                            </FormControl>
                        </Grid> 
                        <Grid item sm={6}>
                            <FormControl
                                fullWidth>
                                <InputLabel>Six Pack</InputLabel>
                                <TextField
                                  label={"Six Pack"}
                                    disabled
                                    name='countSixPacks'
                                    value={meetups.estimatedCount}
                                />
                            </FormControl>
                        </Grid>
                    </Grid>
                </form>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleClose} color="primary">
                    Cancelar
          </Button>
                <Button onClick={handleClose} color="primary" onClick={handleAddMeetup} >
                    Crear
          </Button>
            </DialogActions>
        </Dialog>
        <Grid container spacing={2} className={classes.grid}>
            {meetups?.byId.map(meetup =>
                <Grid item xs={12} sm={12} md={4} xl={4} key={meetup?.name}>
                    <Card className={classes.card} key={meetup?.name} variant="outlined">
                        <CardActionArea>
                            <CardContent>
                                <Typography gutterBottom variant="h5" component="h5">
                                    {meetup?.name}
                                </Typography>
                                <Typography variant="overline" display="block" >
                                    Invitados {meetup?.users.map(u =>
                                    <>
                                        <Chip label={u.username} />
                                    </>
                                )}
                                </Typography>
                                <Typography variant="overline" display="block" >
                                    Estuve? {(meetup?.usersAttended?.find(u => u.username === usuarios.username) != undefined) == true ? "si" : "no"}
                                </Typography>
                                <Typography variant="overline" display="block">
                                    Cantidad de personas {meetup?.countPerson}
                                </Typography>
                                <Typography variant="overline" display="block">
                                    Fecha {meetup?.fecha}
                                </Typography>
                                <Typography variant="overline" display="block">
                                    Temp: {meetup?.temp}°c
                                </Typography>
                                {(usuarios.rol === "ADMIN") ? <Typography variant="overline" display="block">
                                    Cantidad Sixpacks {meetup?.countSixPacks}
                                </Typography> : ""}
                            </CardContent>
                        </CardActionArea>
                        <CardActions>
                            {(usuarios.rol === "ADMIN") ? <Button size="small" color="primary" onClick={() => handleDeleteMeetup(meetup)}>
                                Eliminar
                            </Button> : ""}
                            {/* Mostrar botones solo sino estuve en la meetups */}
                            {(meetup?.users?.find(u => u.username === usuarios.username) != undefined) == true ?
                                "" :
                                <Button size="small" color="primary" onClick={() => handleSubscribeMeetup(meetup)}>
                                    Suscribirme
                                </Button>}

                            {(meetup?.usersAttended?.find(u => u.username === usuarios.username) != undefined) == true ?
                                "" : <Button size="small" color="primary" onClick={() => handleIwasHereMeetup(meetup)}>
                                    Estuve Aquí
                                    </Button>}
                        </CardActions>
                    </Card>
                </Grid>
            )}
        </Grid>
    </Container>
};

const mapStateToProps = state => ({
    meetups: getMeetup(state),
    cities: getCities(state),
    usuarios: getUsuariosSlice(state),
    weather: getWeather(state),
    location: getCurrentLocation(state),
});

const mapDispatchToProps = {
    addMeetup,
    deleteMeetup,
    mounted,
    getEstimatedCount,
    subscribeMeetup,
    iWasHereMeetup,
    getWeatheCity
};

export default connect(mapStateToProps, mapDispatchToProps)(MeetupDashboard)