import { createSelector } from "reselect";

export const getUsuariosSlice = state => state.usuarios;
export const getUsuarios = state => getUsuariosSlice(state).byId;
export const getIsLoggedIn = state => Boolean(getUsuariosSlice(state).username);
export const getIsAttemptingLogin = state => getUsuariosSlice(state).isLoggingIn;
export const getUserName = state => getUsuariosSlice(state).username;
export const getUserRol = state => getUsuariosSlice(state).rol;

export const getUsuariosByIdName = (state) => Object.values(getUsuariosSlice(state).byId)
    .map(({ id, username }) => ({
        id: id,
        name: username
    })
); 